import React from "react"
import Flickity from 'react-flickity-component'
import styled from 'styled-components'


const flickityOptions = {
    initialIndex: 2
}

const SliderContainer = styled.section `
    width:100%;
    overflow:hidden;
`


const Slider = () => (
        <SliderContainer>
        <Flickity
        className={'carousel'} // default ''
        elementType={'div'} // default 'div'
        options={flickityOptions} // takes flickity options {}
        disableImagesLoaded={false} // default false
        reloadOnUpdate // default false
        static // default false
        >
            <img src="https://res.cloudinary.com/dhat0b0ey/image/upload/v1549833774/fa205b5f4333b4a8573997d971f57936.jpg" alt=""/>
            <img src="https://res.cloudinary.com/dhat0b0ey/image/upload/v1549833768/d648f5ccb5dcf8ea5befb52e3250ff83.jpg" alt=""/>
            <img src="https://res.cloudinary.com/dhat0b0ey/image/upload/v1544373816/johndory/General/xl-fresh-bread-waas-bakery-shetland.jpg" alt=""/>
        </Flickity>
        </SliderContainer>
       
)


export default Slider